import { writable } from 'svelte/store';

// export const apiServer = "http://localhost:9000/api/";
// export const apiServer = "https://indexao.herokuapp.com/api/";

Storage.prototype.setObject = function(key, value) {
    this.setItem(key, JSON.stringify(value));
}

Storage.prototype.getObject = function(key) {
    var value = this.getItem(key);
    if (value == "null" || value == "undefined" || value == "") {
        return 0 && JSON.parse(0);
    }
    return value && JSON.parse(value);
}

export const sectionActive = writable(window.localStorage.getObject("sectionActive")) || "";

export const indexsComplete = writable(window.localStorage.getObject("indexsComplete"));
export const indexSelected = writable(0);

export const indexsFx = writable(window.localStorage.getObject("indexsFx"));
