<script>
  import { flagIndexLoaded, indexsComplete, resourcesComplete } from '../../stores.js';
  import { AddGraphic, LoadGraphics } from '../../indexdb.js';

  import { CompileBinaryIndex, CompileIniIndex } from '../logic/indexCompiler.js';
  
  import {
    Button,
    Modal,
    ModalBody,
    ModalFooter,
    ModalHeader,
    Row,
    Col,
    InputGroup,
    InputGroupText,
    FormGroup,
    Label,
    Input,
    Spinner,
    Alert,
  } from 'sveltestrap';

  let open = false;
  let uploading = false;
  let msgError = "";
  let selectFormat = "0130";

  const toggle = () => {
    if (uploading) {
      return;
    }
    msgError = "";
    open = !open;
  }

  async function exportIndex() {


    if (selectFormat == "0130") {
      const blobobject = CompileBinaryIndex($indexsComplete);

			const filename = "graficos_" + Date.now() + ".ind";
			if(window.navigator.msSaveOrOpenBlob) {
				window.navigator.msSaveBlob(blobobject, filename);
			} else {
				const blob = window.URL.createObjectURL(new Blob(blobobject, { type: 'application/octet-stream' }), { oneTimeOnly: true })
				const anchor = document.createElement('a');
				anchor.style.display = 'none';
				anchor.href = blob;
				anchor.download = filename;
				document.body.appendChild(anchor);
				anchor.click();
				window.URL.revokeObjectURL(blob);
				document.body.removeChild(anchor);
        toggle();
			};
    } else if (selectFormat == "ini") {
      const textobject = CompileIniIndex($indexsComplete);
      const blobobject = [textobject];

      const filename = "graficos_" + Date.now() + ".ini";
      if(window.navigator.msSaveOrOpenBlob) {
        window.navigator.msSaveBlob(blobobject, filename);
      } else {
        const blob = window.URL.createObjectURL(new Blob(blobobject, { type: 'application/octet-stream' }), { oneTimeOnly: true })
        const anchor = document.createElement('a');
        anchor.style.display = 'none';
        anchor.href = blob;
        anchor.download = filename;
        document.body.appendChild(anchor);
        anchor.click();
        window.URL.revokeObjectURL(blob);
        document.body.removeChild(anchor);
        toggle();
      };
    };

	}

</script>
  
<div style="width: inherit;">
  <Button color="success" style="width: inherit;" on:click={toggle} >Exportar indice</Button>
  <Modal isOpen={open} {toggle} modalClassName="modal-dark">
    <ModalHeader {toggle}>Exportar indice</ModalHeader>
    <ModalBody>
      Presiona en Exportar para descargar el indice de graficos generado.
      <FormGroup>
        <Label for="exampleSelect">Formato</Label>
        <Input bind:value={selectFormat} type="select" name="selectFormat">
          <option value="ini">Formato INI</option>
          <option value="0130" selected>0.13.x Compatible</option>
        </Input>
      </FormGroup>
    </ModalBody>
    <ModalFooter>
      {#if (msgError)}
        <Row><Alert color="danger">{msgError}</Alert></Row>
      {/if}
      {#if !uploading} 
        <Row>
          <Col><Button color="primary" on:click={exportIndex}>Exportar</Button></Col>
          <Col><Button color="secondary" on:click={toggle}>Cancelar</Button></Col>
        </Row>
      {:else}
        <Spinner primary  />
      {/if}
    </ModalFooter>
  </Modal>
</div>