<script>
	import { onDestroy, onMount } from "svelte";
	import { Row, Col, Badge, Alert, Image, ListGroup, ListGroupItem, 
		Spinner, InputGroup, Input, InputGroupText, Nav, Navbar, NavbarBrand, 
		NavItem, NavLink, Button } from 'sveltestrap';

	import { indexsComplete, resourcesComplete,  flagIndexLoaded, 
		flagResourcesLoaded, indexSelected, sectionActive,
		indexsFx, indexsHead, indexsHelmet, indexsBodies
	} from '../stores.js';
	import { LoadGraphics } from '../indexdb.js';

	import Landing from "./landing.svelte";

	import ModalLoadResources from '../components/modalLoadResources.svelte';
	import IndexListRight from '../components/studio/indexListRight.svelte';
	import IndexListLeft from '../components/studio/indexListLeft.svelte';
	import IndexEdit from '../components/studio/indexEdit.svelte';

	import IndexHeadListRight from '../components/studio/indexHeadListRight.svelte';
	import IndexHeadEdit from '../components/studio/indexHeadEdit.svelte';

	import IndexHelmetListRight from '../components/studio/indexHelmetListRight.svelte';
	import IndexHelmetEdit from '../components/studio/indexHelmetEdit.svelte';

	import IndexBodiesListRight from '../components/studio/indexBodiesListRight.svelte';
	import IndexBodiesEdit from '../components/studio/indexBodiesEdit.svelte';

	import IndexFxListRight from '../components/studio/indexFxListRight.svelte';
	import IndexFxEdit from '../components/studio/indexFxEdit.svelte';

	let messageError = "";
	let showDebug = false;

	function loadInd() {
		messageError = "";
		indexSelected.set(undefined);
		LoadGraphics();
	}

	onMount(async () => {
		loadInd();
	});

</script>

{#if (messageError != "")}
	<Row>
		<Col class="input-dark">
			<Alert color="danger">
				{messageError}
			</Alert>
		</Col>
	</Row>
{/if}

{#if (!$flagResourcesLoaded && !$indexsComplete)}
	<Row>
		<Col class="text-center" style="padding: 2em; color: white;">
			<div style="padding-bottom: 1em;">Se requiere la carga de recursos. 
				<ModalLoadResources mode="button" />
			</div>
		</Col>
	</Row>

	<Landing />
{/if}

{#if ($flagResourcesLoaded || $indexsComplete)}
	<Row cols={{ md: 3, sm: 1 }}>
		<IndexListLeft/>
		{#if ($sectionActive == '')}
			<div class="relative">
				<IndexEdit />
			</div>
			<IndexListRight />
		{:else if $sectionActive == 'head'}
			<div class="relative">
				<IndexHeadEdit />
			</div>
			<IndexHeadListRight />
		{:else if $sectionActive == 'helmet'}
			<div class="relative">
				<IndexHelmetEdit />
			</div>
			<IndexHelmetListRight />
		{:else if $sectionActive == 'body'}
			<div class="relative">
				<IndexBodiesEdit />
			</div>
			<IndexBodiesListRight />
		{:else if $sectionActive == 'fx'}
			<div class="relative">
				<IndexFxEdit />
			</div>
			<IndexFxListRight />
		{/if}
	</Row>	
{/if}

<Row>
	<Col>
		<span on:click={() => showDebug = !showDebug} class="cursor">Click to show Debug</span>
		{#if showDebug}<br/><br/>
		IndexLoaded: {$flagIndexLoaded} (total: {($indexsComplete != undefined && ($indexsComplete != null && $indexsComplete.indexs != null)) ? $indexsComplete.indexs.length : 0})<br/>
		ResourcesLoaded: {$flagResourcesLoaded} (total: {($resourcesComplete != undefined && $indexsComplete != null) ? $resourcesComplete.length : 0})<br/>
		IndexsHead: {($indexsHead != undefined && $indexsHead != null) ? $indexsHead.length : 0}<br/>
		IndexsHelmet: {($indexsHelmet != undefined && $indexsHelmet != null) ? $indexsHelmet.length : 0}<br/>
		IndexsBodies: {($indexsBodies != undefined && $indexsBodies != null) ? $indexsBodies.length : 0}<br/>
		IndexsFx: {($indexsFx != undefined && $indexsFx != null) ? $indexsFx.length : 0}<br/>
		{/if}
	</Col>
</Row>

<style>
	.relative {
		position: relative;
	}
</style>